.icon {
  display: block;
  width: 24px;
  height: 24px;
  background-size: cover;

  @include on-tablet {
    height: 32px;
    width: 32px;
  }

  &--phone {
    background-image: url('/src/images/icon/icon-phone-call.png');
  }

  &--menu {
    background-image: url('/src/images/icon/icon-burger-menu.png');

    &:hover {
      background-image: url('/src/images/icon/icon-burger-menu-hover.png');
    }
  }

  &--close {
    background-image: url('/src/images/icon/icon-close.png');
  }
}
